body {
  margin: 0;
  font-family: 'Fira Code', monospace;
  background: linear-gradient(to right, #fbc2eb, #a6c1ee);
  min-height: 100vh;
  display: flex;
  justify-content: center;
  padding: 40px;
}

.container {
  background-color: #ffffffdd;
  border-radius: 18px;
  padding: 30px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
  width: 90%;
  max-width: 900px;
}

h1 {
  text-align: center;
  color: #ff3e94;
  margin-bottom: 25px;
}

label {
  font-weight: 600;
  margin-top: 20px;
  display: block;
  color: #444;
}

textarea {
  width: 100%;
  min-height: 100px;
  padding: 12px;
  border-radius: 12px;
  border: 2px solid #ddd;
  font-size: 14px;
  margin-top: 6px;
  background: #f9f9f9;
  resize: vertical;
  box-sizing: border-box;
}

button {
  margin: 24px auto;
  display: block;
  background: linear-gradient(to right, #36d1dc, #5b86e5);
  color: white;
  border: none;
  padding: 14px 30px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 10px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s;
}

button:hover {
  transform: scale(1.03);
  background: linear-gradient(to right, #5b86e5, #36d1dc);
}

.output-box {
  background: #f0f0ff;
  border: 2px dashed #bbb;
  color: #222;
  padding: 16px;
  min-height: 80px;
  white-space: pre-wrap;
  border-radius: 12px;
  margin-top: 6px;
}
